@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";
.skill{
    width: 250px;
    background-color: $bgColor;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
    .icon{
        width:60px;
        height: 60px;
        background-color: $primaryColor;
    }
    .skillTitle{
      @include text($primaryFont,30px,1.2);

    }
    .description{
        @include text($secondFont,12px,1.8);
    }

}



